import { Injectable } from '@angular/core';
import {
  ApplicationService,
  GrantType, IAuthenticationRestrictions,
  IResult, ISessionConfiguration,
  ISystemOption,
  ITenantLoginOption,
  ITenantOption,
  SystemOptionsService,
  TenantLoginOptionsService,
  TenantLoginOptionType,
  TenantOptionsService,
  UserManagementSource
} from '@c8y/client';
import { catchError, map } from 'rxjs/operators';
import { forkJoin, from, Observable, of } from 'rxjs';
import { defaults, omit } from 'lodash-es';
import { AuthConfiguration, Options } from './auth-configuration.model';
import { TenantUiService } from '@c8y/ngx-components';

@Injectable()
export class AuthConfigurationService {
  private systemOptionsWithDefaultValue: ISystemOption[] = [
    { category: 'password', key: 'limit.validity', value: null },
    { category: 'password', key: 'enforce.strength', value: 'false' },
    { category: 'two-factor-authentication', key: 'tenant-scope-settings.enabled', value: 'false' },
    { category: 'two-factor-authentication', key: 'enabled', value: 'false' },
    { category: 'two-factor-authentication', key: 'enforced', value: 'false' },
    { category: 'two-factor-authentication', key: 'enforced.group', value: '' }
  ];

  private tenantOptionsWithDefaultValue: ITenantOption[] = [
    { category: 'password', key: 'limit.validity', value: '0' },
    { category: 'password', key: 'strength.validity', value: 'false' },
    { category: 'two-factor-authentication', key: 'enabled', value: 'false' },
    { category: 'two-factor-authentication', key: 'token.validity', value: '43200' }, // 30 days
    { category: 'two-factor-authentication', key: 'pin.validity', value: '30' },
    { category: 'two-factor-authentication', key: 'enforced', value: 'false' },
    { category: 'two-factor-authentication', key: 'strategy', value: 'SMS' },
    { category: 'oauth.internal', key: 'basic-token.lifespan.seconds', value: null }
  ];

  constructor(
    private tenantLoginOptionsService: TenantLoginOptionsService,
    private tenantOptionsService: TenantOptionsService,
    private systemOptionsService: SystemOptionsService,
    private applicationService: ApplicationService,
    private tenantUiService: TenantUiService
  ) {}

  getAuthConfiguration$(): Observable<AuthConfiguration> {
    const loginOptions$ = this.getLoginOptions$();
    return forkJoin({
      loginOptions: loginOptions$,
      tenantOptions: this.getTenantOptions$(),
      systemOptions: this.getSystemOptions$(),
      smsGatewayAvailable: this.isSmsApplicationAvailable$(),
      preferredLoginOptionType: this.getPreferredLoginOptionType$(loginOptions$)
    });
  }

  save(newAuthConfiguration: AuthConfiguration, previousAuthConfiguration: AuthConfiguration) {
    const tenantOptions = this.prepareTenantOptions(
      newAuthConfiguration,
      previousAuthConfiguration
    );
    const updateTenantOptions = tenantOptions.map(
      tenantOption => this.tenantOptionsService.create(tenantOption)
    );
    const basicLoginOption = this.prepareBasicLoginOption(
      newAuthConfiguration,
      previousAuthConfiguration
    );
    const oauthInternalLoginOption = this.prepareOauthInternalLoginOption(
      newAuthConfiguration,
      previousAuthConfiguration
    );

    return Promise.all([
      this.saveOrUpdateLoginOption(basicLoginOption),
      this.saveOrUpdateLoginOption(oauthInternalLoginOption),
      updateTenantOptions
    ]);
  }

  private saveOrUpdateLoginOption(
    loginOption: ITenantLoginOption
  ): Promise<IResult<ITenantLoginOption>> {
    return loginOption.id
      ? this.tenantLoginOptionsService.update(loginOption)
      : this.tenantLoginOptionsService.create(loginOption);
  }

  private prepareBasicLoginOption(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantLoginOption {
    let basicLoginOption = this.originalLoginOptionWithDefaults(
      previousAuthConfiguration,
      TenantLoginOptionType.BASIC
    );
    basicLoginOption.authenticationRestrictions =
      this.authenticationRestriction(newAuthConfiguration);
    basicLoginOption.visibleOnLoginPage = this.visibleOnLoginPage(
      newAuthConfiguration,
      TenantLoginOptionType.BASIC
    );
    basicLoginOption = this.removeReadOnlyFields(basicLoginOption);
    return basicLoginOption;
  }

  private prepareOauthInternalLoginOption(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantLoginOption {
    let oauthInternalLoginOption = this.originalLoginOptionWithDefaults(
      previousAuthConfiguration,
      TenantLoginOptionType.OAUTH2_INTERNAL
    );
    const sessionConfiguration = this.sessionConfiguration(newAuthConfiguration);
    sessionConfiguration !== null
      ? (oauthInternalLoginOption.sessionConfiguration = sessionConfiguration)
      : delete oauthInternalLoginOption.sessionConfiguration;
    oauthInternalLoginOption.visibleOnLoginPage = this.visibleOnLoginPage(
      newAuthConfiguration,
      TenantLoginOptionType.OAUTH2_INTERNAL
    );
    oauthInternalLoginOption = this.removeReadOnlyFields(oauthInternalLoginOption);
    return oauthInternalLoginOption;
  }

  private originalLoginOptionWithDefaults(
    previousAuthConfiguration: AuthConfiguration,
    loginOptionType: TenantLoginOptionType
  ): ITenantLoginOption {
    return defaults(
      {},
      previousAuthConfiguration.loginOptions.find(
        loginOption => loginOption.type === loginOptionType
      ),
      this.getDefaultLoginOption(loginOptionType)
    );
  }

  private sessionConfiguration(authConfiguration: AuthConfiguration): ISessionConfiguration {
    return authConfiguration.loginOptions.find(this.tenantUiService.isOauthInternal)
      .sessionConfiguration;
  }

  private authenticationRestriction(authConfiguration: AuthConfiguration): IAuthenticationRestrictions {
    const authenticationRestrictions = authConfiguration.loginOptions.find(
      this.tenantUiService.isBasic
    ).authenticationRestrictions;
    return {
      trustedUserAgents: authenticationRestrictions.trustedUserAgents.filter(value => value),
      forbiddenUserAgents: authenticationRestrictions.forbiddenUserAgents.filter(value => value),
      forbiddenClients: authenticationRestrictions.forbiddenClients.filter(value => value)
    };
  }

  private visibleOnLoginPage(
    authConfiguration: AuthConfiguration,
    loginOptionType: TenantLoginOptionType
  ): boolean {
    return authConfiguration.preferredLoginOptionType === loginOptionType;
  }

  private removeReadOnlyFields(tenantLoginOption: ITenantLoginOption): ITenantLoginOption {
    return omit(tenantLoginOption, [
      'self',
      'strengthValidity',
      'tfaStrategy',
      'greenMinLength',
      'enforceStrength',
      'strengthValidity',
      '_type'
    ]);
  }

  private prepareTenantOptions(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantOption[] {
    const getValue = (authCfg, tenantOption) =>
      authCfg.tenantOptions[tenantOption.category][tenantOption.key];
    const hasChanged = tenantOption =>
      getValue(newAuthConfiguration, tenantOption) !==
      getValue(previousAuthConfiguration, tenantOption);

    return this.tenantOptionsWithDefaultValue
      .filter( tenantOption => getValue(newAuthConfiguration, tenantOption) !== null)
      .filter(tenantOption => hasChanged(tenantOption))
      .map(tenantOption => ({
        category: tenantOption.category,
        key: tenantOption.key,
        value: getValue(newAuthConfiguration, tenantOption).toString()
      }));
  }

  private getLoginOptions$(): Observable<ITenantLoginOption[]> {
    return from(this.tenantLoginOptionsService.listForCurrentTenant()).pipe(
      map(res => res.data),
      map(loginOptions => this.addDefaultLoginOptions(loginOptions))
    );
  }

  private getPreferredLoginOptionType$(loginOptions$: Observable<ITenantLoginOption[]>): Observable<TenantLoginOptionType> {
    return loginOptions$.pipe(
      map(loginOptions => {
        return this.tenantUiService.getPreferredLoginOption(loginOptions).type;
      })
    );
  }

  private addDefaultLoginOptions(loginOptions: ITenantLoginOption[]) {
    if (!loginOptions.find(this.tenantUiService.isBasic)) {
      loginOptions.push(this.getDefaultLoginOption(TenantLoginOptionType.BASIC));
    }
    if (!loginOptions.find(this.tenantUiService.isOauthInternal)) {
      loginOptions.push(this.getDefaultLoginOption(TenantLoginOptionType.OAUTH2_INTERNAL));
    }
    return loginOptions;
  }

  private getTenantOptions$(): Observable<Options> {
    return forkJoin(
      this.tenantOptionsWithDefaultValue.map((option: ITenantOption) =>
        from(this.tenantOptionsService.detail(option)).pipe(
          map(res => res.data),
          catchError(() => of(option))
        )
      )
    ).pipe(map(options => this.getOptionsObject(options)));
  }

  private getSystemOptions$(): Observable<Options> {
    return forkJoin(
      this.systemOptionsWithDefaultValue.map((option: ISystemOption) =>
        from(this.systemOptionsService.detail(option)).pipe(
          map(res => res.data),
          catchError(() => of(option))
        )
      )
    ).pipe(map(options => this.getOptionsObject(options)));
  }

  private isSmsApplicationAvailable$(): Observable<boolean> {
    return from(this.applicationService.isAvailable('sms-gateway')).pipe(map(res => res.data));
  }

  private getOptionsObject(options: Array<ITenantOption | ISystemOption>) {
    return options.reduce((optionsObject, option) => {
      optionsObject[option.category] = optionsObject[option.category] || {};
      optionsObject[option.category][option.key] = this.getValue(option);
      return optionsObject;
    }, {});
  }

  private getValue(option: ITenantOption | ISystemOption) {
    try {
      return JSON.parse(option.value);
    } catch (e) {
      return option.value;
    }
  }

  private getDefaultLoginOption(tenantLoginOptionType: TenantLoginOptionType): ITenantLoginOption {
    return {
      userManagementSource: UserManagementSource.INTERNAL,
      grantType: GrantType.PASSWORD,
      providerName: 'Cumulocity',
      visibleOnLoginPage: false,
      type: tenantLoginOptionType
    };
  }
}
